import React from 'react'
import Assets from './Assets'
import Cameras from './Cameras'
import Entities from './Entities'

class MainCreative extends React.PureComponent {
  constructor(props){
    super();
    this.state={
      loadedContent:null,
    }
  }
  componentDidMount(){
    import('aframe')
      .then((aframe) => {
        if(!!!aframe.components['video-texture']){
          console.log(`AFRAME.utils.device.isMobile=${aframe.utils.device.isMobile()}`);
          aframe.registerComponent("video-texture", {
            init: function() {
              this.el.addEventListener("click", (e) => {
                console.log(document.querySelector(this.el.getAttribute('src')).paused);
                if(document.querySelector(this.el.getAttribute('src')).paused){
                  document.querySelector(this.el.getAttribute('src')).play()
                }else{
                  document.querySelector(this.el.getAttribute('src')).pause()
                }
              })
            }
          })
          aframe.registerComponent("plane-texture", {
            init: function() {
              this.el.addEventListener("click", (e) => {
                if(this.el.sceneEl.parentNode.id==='asceneHolder'){
                  document.querySelector('#___gatsby').appendChild(this.el.sceneEl);
                  // this.el.sceneEl.removeAttribute('embedded');
                }else{
                  document.querySelector('#asceneHolder').appendChild(this.el.sceneEl);
                  // this.el.sceneEl.setEntityAttribute('embedded',true);
                  // this.el.sceneEl.setAttribute('embedded');
                  //  ('embedded')
                }
                console.log(this.el.sceneEl.parentNode.id);
              })
            }
          })
        }
        this.setState({
          loadedContent:(
            <main id="asceneHolder">
              <a-scene cursor="rayOrigin: mouse" embedded style={{width:'100vw',height:'100vh'}}>
                <Assets />
                <Entities />
                <Cameras />
              </a-scene>
            </main>
          )})
          // aframe.ANIME.timeline({   embedded style={{width:'100%',height:'100%'}}
          //   targets:document.querySelector('#rig'),
          //   position: [650,0,600],
          //   easing: 'easeInOutSine',
          //   endDelay: 1000 ___gatsby
          // })
      })
      .catch((error) => console.error(error));
  }
  render() {
    return this.state.loadedContent;
  }
}

export default MainCreative
